import {
  IonBackButton,
  IonButton,
  IonButtons,
  IonCheckbox,
  IonContent,
  IonHeader,
  IonIcon,
  IonList,
  IonMenuButton,
  IonPage,
  IonSearchbar,
  IonToolbar,
  useIonAlert,
  useIonRouter,
} from "@ionic/react";
import React, { useEffect, useRef, useState } from "react";
import Tweet from "../components/Tweet";
import useStore, { tweetsProps } from "../util/store";
import { Virtuoso } from "react-virtuoso";
import {
  checkmarkDoneCircle,
  checkmarkDoneOutline,
  saveOutline,
  trashOutline,
} from "ionicons/icons";

interface newTweetsProps extends tweetsProps {
  checked?: boolean;
}

const Tweets: React.FC = () => {
  const router = useIonRouter();
  const windowHeight = useRef(window.innerHeight).current;
  const { tweets: storeTweets, setState } = useStore();
  const [tweets, setTweets] = useState<newTweetsProps[] | []>(
    storeTweets || []
  );
  const [multipleSelect, setMultipleSelect] = useState(false);
  const searchBarRef = useRef(null);

  const [present] = useIonAlert();

  useEffect(() => {}, [storeTweets]);
  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <IonButtons>
            <IonBackButton defaultHref="home" />
            {tweets && tweets.length > 0 && (
              <IonButton
                onClick={() =>
                  present({
                    cssClass: "my-css",
                    header: "Delete All tweets",
                    message: "Are you sure?",
                    buttons: [
                      "No",
                      {
                        text: "Yes",
                        handler: () => {
                          setTweets([]);
                          setState({ tweets: [], activeTweet: [] });
                        },
                      },
                    ],
                  })
                }
              >
                <IonIcon icon={trashOutline} />
              </IonButton>
            )}
            <IonSearchbar
              ref={searchBarRef}
              style={{ paddingBottom: 1 }}
              showCancelButton="focus"
              onIonChange={(ev) => {
                const value = ev.detail.value;
                if (!storeTweets) return;
                if (!value?.trim()) setTweets(storeTweets);
                setTweets(
                  storeTweets?.filter((t) => t.fullText.includes(value || ""))
                );
              }}
              onIonClear={() => {
                if (!storeTweets) return;
                setTweets(storeTweets);
              }}
              mode="ios"
              placeholder="Search in tweets ..."
              animated
            />
            {multipleSelect && (
              <IonButton
                onClick={() => {
                  const newActiveTweets = tweets.filter((t) => t.checked);
                  if (newActiveTweets && newActiveTweets.length > 0) {
                    const lastTweet = newActiveTweets.slice(-1)[0];
                    // Clipboard.write({
                    //   string: `https://twitter.com/${lastTweet.userId}/status/${lastTweet.tweetId}`,
                    // });
                    setState({ activeTweet: newActiveTweets });
                    router.push("/home");
                  }
                  setMultipleSelect(false);
                }}
              >
                <IonIcon icon={saveOutline} />
              </IonButton>
            )}
            <IonButton
              onClick={() => {
                if (!multipleSelect) {
                  if (!tweets) return;
                  let hasChecked = false;
                  for (const t of tweets) {
                    if (t.checked) {
                      hasChecked = true;
                      t.checked = false;
                    }
                  }
                  if (hasChecked) setTweets(tweets);
                }
                setMultipleSelect(!multipleSelect);
              }}
            >
              <IonIcon
                icon={
                  multipleSelect ? checkmarkDoneCircle : checkmarkDoneOutline
                }
              />
            </IonButton>
            <IonMenuButton style={{ marginLeft: "auto" }} />
          </IonButtons>
        </IonToolbar>
      </IonHeader>

      <IonContent fullscreen>
        <IonList style={{ padding: 0 }}>
          <Virtuoso
            data={tweets}
            style={{ height: windowHeight - 56, width: "100%" }}
            itemContent={(index: number, tweet: newTweetsProps) => (
              <div
                style={{
                  background: "#15202b",
                  padding: 16,
                  display: "flex",
                  alignItems: "center",
                  overflowX: "hidden",
                }}
                key={tweet.userId}
              >
                <Tweet canRemove data={tweet} />
                {multipleSelect && (
                  <div style={{ paddingLeft: 16 }}>
                    <IonCheckbox
                      checked={tweet.checked}
                      onIonChange={(e) => {
                        if (!tweets) return;
                        for (const t of tweets) {
                          if (t.tweetId == tweet.tweetId) {
                            t.checked = e.detail.checked;
                          }
                        }
                        setTweets(tweets);
                      }}
                    ></IonCheckbox>
                  </div>
                )}
              </div>
            )}
          />
        </IonList>
      </IonContent>
    </IonPage>
  );
};

export default Tweets;
