import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonButtons,
  IonMenuButton,
  IonContent,
  IonBackButton,
  IonButton,
  IonIcon,
  useIonRouter,
  IonProgressBar,
} from "@ionic/react";
import { useCallback, useEffect, useRef, useState } from "react";
import { useParams } from "react-router";
import ReactCrop, { Crop } from "react-image-crop";
import "./CropImage.css";
import { saveOutline } from "ionicons/icons";
import useStore from "../util/store";

function getnewImagesImg(image: HTMLImageElement, crop: any) {
  const canvas = document.createElement("canvas");
  const scaleX = image.naturalWidth / image.width;
  const scaleY = image.naturalHeight / image.height;
  canvas.width = crop.width as number;
  canvas.height = crop.height as number;
  const ctx = canvas.getContext("2d");

  ctx?.drawImage(
    image,
    crop.x * scaleX,
    crop.y * scaleY,
    crop.width * scaleX,
    crop.height * scaleY,
    0,
    0,
    crop.width,
    crop.height
  );

  // As Base64 string
  const base64Image = canvas.toDataURL("image/png");
  return base64Image;

  // As a blob
  // return new Promise((resolve, reject) => {
  //   canvas.toBlob(
  //     (blob) => {
  //       if (!blob)
  //       blob.name = fileName;
  //       resolve(blob);
  //     },
  //     "image/jpeg",
  //     1
  //   );
  // });
}

const CropImage: React.FC = () => {
  const { activeTweet } = useStore((st) => st);
  const { link: ImageLink } = useParams<{ link: string }>();
  const activeImage: { link: string; tweetID: string; imageIndex: number } =
    JSON.parse(atob(ImageLink));
  const imgRef = useRef<HTMLImageElement | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [hasChanged, setHasChanged] = useState(false);
  const [crop, setCrop] = useState<Crop>({
    //aspect: 1,
    unit: "%",
    width: 100,
    height: 80,
    x: 0,
    y: 10,
  });
  const router = useIonRouter();
  const onLoad = useCallback((img) => {
    imgRef.current = img;
    setIsLoading(false);
    return false;
  }, []);
  return (
    <IonPage>
      <IonHeader>
        <IonToolbar>
          <div className="header-neon neon">
            <IonBackButton defaultHref="home" />
            <span style={{ marginLeft: 16 }}>Crop Image</span>
            <IonButtons style={{ marginLeft: "auto" }}>
              <IonButton
                onClick={() => {
                  if (imgRef.current && activeTweet && hasChanged) {
                    for (const tweet of activeTweet) {
                      if (tweet.tweetId == activeImage.tweetID) {
                        const newImage = getnewImagesImg(
                          imgRef.current as HTMLImageElement,
                          crop
                        );
                        // if (!newImages[activeImage.tweetID])
                        //   newImages[activeImage.tweetID] = {};
                        // if (
                        //   !newImages[activeImage.tweetID][
                        //     activeImage.imageIndex
                        //   ]
                        // )
                        //   newImages[activeImage.tweetID][
                        //     activeImage.imageIndex
                        //   ] = { link: "" };
                        // newImages[activeImage.tweetID][
                        //   activeImage.imageIndex
                        // ].link = newImage;
                        // setImages({ newImages });
                      }
                    }
                  }
                  router.goBack();
                }}
              >
                <IonIcon icon={saveOutline} />
              </IonButton>
              <IonMenuButton />
            </IonButtons>
          </div>
        </IonToolbar>
      </IonHeader>
      <IonContent>
        {isLoading && (
          <IonProgressBar
            style={{ position: "absolute", zIndex: "99", top: 0 }}
            type="indeterminate"
          ></IonProgressBar>
        )}
        <div
          style={{
            position: "relative",
            margin: "auto",
            display: "flex",
            maxWidth: "90%",
            height: "100%",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          <ReactCrop
            crossorigin="anonymous"
            keepSelection
            onImageLoaded={onLoad}
            src={activeImage.link}
            crop={crop}
            onChange={(newCrop) => {
              !hasChanged && setHasChanged(true);
              setCrop(newCrop);
            }}
          />
        </div>
      </IonContent>
    </IonPage>
  );
};

export default CropImage;
