import {
  useIonRouter,
  IonButton,
  IonText,
  useIonPopover,
  IonItem,
  IonList,
  IonIcon,
  IonLabel,
} from "@ionic/react";
import {
  ellipsisVertical,
  eyeOutline,
  logoTwitter,
  trashOutline,
} from "ionicons/icons";
import React, { useRef } from "react";
import useStore, { tweetsProps } from "../util/store";
import { useParams } from "react-router";
type tweetProps = {
  data: tweetsProps;
  mode?: "small" | "extended";
  canRemove?: boolean;
};

const Tweet: React.FC<tweetProps> = ({
  data,
  mode = "small",
  canRemove = false,
}) => {
  const windowWidth = useRef(window.innerWidth).current;
  const router = useIonRouter();
  const { setState, tweets, activeTweet } = useStore((st) => st);
  const [present, dismiss] = useIonPopover(
    <IonList style={{ "--ion-item-background": "#15202b" }}>
      <IonItem
        button
        onClick={() => {
          router.push("/tweet-details/" + data.tweetId);
          dismiss();
        }}
      >
        <IonLabel>View tweet</IonLabel>
        <IonIcon icon={eyeOutline} />
      </IonItem>
      <IonItem
        button
        onClick={() => {
          let lastState = { activeTweet: [data], tweets };
          if (!tweets?.some((tweet) => tweet.tweetId == data.tweetId)) {
            let myTweets = tweets || [];
            myTweets.unshift(data);
            lastState.tweets = myTweets;
          }
          // Clipboard.write({
          //   string: `https://twitter.com/${data.userId}/status/${data.tweetId}`,
          // });
          setState(lastState);
          router.push("/home");
          dismiss();
        }}
      >
        <IonLabel>Set as active</IonLabel>
        <IonIcon icon={logoTwitter} />
      </IonItem>
      {canRemove && (
        <IonItem
          button
          onClick={() => {
            const itemIndexInTweets = tweets?.findIndex(
              (tweet) => (tweet.tweetId = data.tweetId)
            );
            const itemIndexInActiveTweet = activeTweet?.findIndex(
              (tweet) => (tweet.tweetId = data.tweetId)
            );
            let myActiveTweet;
            if (itemIndexInActiveTweet) {
              myActiveTweet = activeTweet || [];
              myActiveTweet.splice(itemIndexInActiveTweet, 1);
            }
            if (itemIndexInTweets == undefined) return;
            let myTweets = tweets || [];
            myTweets.splice(itemIndexInTweets, 1);
            let lastState = { tweets: myTweets, activeTweet };
            if (myActiveTweet) lastState.activeTweet = myActiveTweet;
            setState(lastState);
            // router.push("/home");
            dismiss();
          }}
        >
          <IonLabel>Remove tweet</IonLabel>
          <IonIcon color="danger" icon={trashOutline} />
        </IonItem>
      )}
    </IonList>,
    {
      onHide: () => dismiss(),
    }
  );

  const { id: ID } = useParams<{ id: string }>();

  const setTweet = () => {
    //check if exists
    let tweet: tweetsProps | undefined;
    let myTweets = tweets || [];
    for (const t of myTweets) {
      if (t.tweetId == data.tweetId) {
        tweet = t;
        break;
      }
    }

    //tweet existed
    if (tweet) {
      console.log("tweet existed!");
      setState({
        activeTweet: [tweet],
      });
    } //tweet not existed
    else {
      console.log("tweet is new, grabbing it ...");
      myTweets.unshift(data);
      setState({
        tweets: myTweets,
        activeTweet: [data],
      });
    }
  };
  return (
    <div
      onClick={() => {
        // if (data.tweetId != ID) {
        //   router.push("/tweet-details/" + data.tweetId);
        // }
      }}
      style={{ width: "100%", padding: "8px 0", marginTop: 8 }}
    >
      <div
        className="tweet-header"
        style={{
          display: "flex",
          alignItems: "center",
          width: "100%",
        }}
      >
        <div
          style={{
            boxShadow: "rgb(0 0 0 / 2%) 0px 0px 2px inset",
            display: "flex",
            alignItems: "center",
            width: 48,
            height: 48,
            flexShrink: 0,
          }}
        >
          <img
            src={data.profileImage}
            style={{
              width: 48,
              height: 48,
              borderRadius: "100%",
            }}
          />
        </div>

        <div
          className="tweet-name-username"
          style={{
            marginLeft: 16,
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
          }}
        >
          <div
            //className={`${blurred && " blurred"}`}
            style={{
              fontWeight: 700,
              width: "120%",
              color: "white",
              direction: mode == "small" ? "ltr" : "rtl",
              textAlign: "left",
            }}
          >
            {data.name}
            {mode == "small" && (
              <span
                style={{
                  color: " rgb(136, 153, 166)",
                  fontSize: 14,
                  fontWeight: "normal",
                }}
              >
                {" " + data.userName}
              </span>
            )}
          </div>
          {mode == "extended" && (
            <div
              style={{
                color: " rgb(136, 153, 166)",
                fontSize: 14,
              }}
            >
              {data.userName}
            </div>
          )}

          {data.mentions.length > 0 && mode == "small" && (
            <div
              className="subHeader"
              style={{
                fontSize: 14,
                width: "100%",
              }}
            >
              <span style={{ color: "rgb(136, 153, 166)" }}>Replying to</span>
              {data.mentions.map((mention, index) => (
                <IonText key={mention.userId}>
                  {index > 0 ? (
                    <>
                      <IonText
                        style={{
                          color: "rgb(136, 153, 166)",
                        }}
                      >
                        {" "}
                        and{" "}
                      </IonText>
                      <IonText color="primary">@{mention.userName}</IonText>
                    </>
                  ) : (
                    <IonText color="primary">{" @" + mention.userName}</IonText>
                  )}
                </IonText>
              ))}
            </div>
          )}
        </div>
        <IonButton
          expand="full"
          style={{
            marginLeft: "auto",
            "--color": "white",
            "--color-focused": "var(--ion-color-primary, #3880ff)",
            "--color-hover": "var(--ion-color-primary, #3880ff)",
            "--padding-start": 0,
            "--padding-end": 0,

            width: "unset",
          }}
          onClick={(e) =>
            present({
              event: e.nativeEvent,
              showBackdrop: false,
              cssClass: "popover-mochol",
            })
          }
          fill="clear"
          shape="round"
        >
          <img
            src={ellipsisVertical.replace(
              "class='ionicon'",
              `class='ionicon' style='fill: white'`
            )}
            color="white"
            style={{ width: 20 }}
          />
        </IonButton>
      </div>

      {data.mentions.length > 0 && mode == "extended" && (
        <div
          className="subHeader"
          style={{
            fontSize: 14,
            width: "100%",
            padding: "16px 0",
          }}
        >
          <span style={{ color: "rgb(136, 153, 166)" }}>Replying to</span>
          {data.mentions.map((mention, index) => (
            <IonText key={mention.userId}>
              {index > 0 ? (
                <>
                  <IonText
                    style={{
                      color: "rgb(136, 153, 166)",
                    }}
                  >
                    {" "}
                    and{" "}
                  </IonText>
                  <IonText color="primary">@{mention.userName}</IonText>
                </>
              ) : (
                <IonText color="primary">{" @" + mention.userName}</IonText>
              )}
            </IonText>
          ))}
        </div>
      )}
      <div className="tweet-body" style={{ width: "100%", paddingTop: 8 }}>
        <div
          style={{
            outline: "none",
            color: "white",
            direction: "rtl",
            //whiteSpace: "pre",
            fontSize: mode == "extended" ? 18 : 14,
          }}
        >
          {data.fullText}
        </div>

        <div
          className="media"
          style={{
            paddingTop: 16,
            justifyContent: "center",
            display: "flex",
            flexWrap: "wrap",
          }}
        >
          {data.media.map((media) => (
            <div
              key={media.url}
              style={{
                borderColor: "rgb(56, 68, 77)",
                margin: "auto",
                maxWidth: "50%",
              }}
            >
              <img
                key={media + "img"}
                style={{
                  width: "100%",
                  borderRadius: 16,
                  maxHeight: windowWidth / 2,
                }}
                src={media.url}
              />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Tweet;
